@keyframes unyonnyo {
  0% {
    transform: scale(1.08, 0.92);
  }
  50% {
    transform: scale(0.92, 1.08);
  }
  100% {
    transform: scale(1.08, 0.92);
  }
}
@keyframes unyonnyo2 {
  0% {
    transform: scale(0.92, 1.08);
  }
  50% {
    transform: scale(1.08, 0.92);
  }
  100% {
    transform: scale(0.92, 1.08);
  }
}
@keyframes poyopoyo {
  0% {
    top: 6px;
    left: 6px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: 6px;
    left: 6px;
  }
}
@keyframes poyopoyo2 {
  0% {
    top: -7px;
    left: -7px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: -7px;
    left: -7px;
  }
}
@keyframes poyopoyo_mini {
  0% {
    top: 2px;
    left: 2px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: 2px;
    left: 2px;
  }
}
@keyframes poyopoyo2_mini {
  0% {
    top: -3px;
    left: -3px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: -3px;
    left: -3px;
  }
}
